.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #00000088;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #1f1f1f;
  padding: 40px;
  height: 550px;
  width: 400px;
  color: #fff;
  border-radius: 10px;
  font-size: 18px;
  
  &__content {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    &-title {
      font-weight: 700;
    }

    &-close {
      cursor: pointer;
      width: 20px;
      height: 20px;
      background-image: url(../images/cross.jpg);
      background-repeat: no-repeat;
      background-size: contain;

      &-link {
        display: block;
        width: 100%;
        height: 100%;
      }
    }
  }
  
  &__field {
    display: flex;
    flex-direction: column;
    font-weight: 700;

    &-input {
      padding: 0 10px;
      height: 30px;
      border: none;
      border-radius: 4px;
      outline: none;
      font-size: 18px;
    }
  }

  &__category {
    &-input {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    &-select {
      cursor: pointer;
      font-size: 18px;
      border: none;
      outline: none;
    }
  }

  &__submit {
    cursor: pointer;
    font-weight: 900;
    height: 30px;
    background-color: #fff;
    border-radius: 5px;
    color: #1f1f1f;
    border: 1px solid #fff;
    transition: 300ms;
    width: 100%;

    &:hover {
      background-color: transparent;
      color: #fff;
    }
  }
}